
.navArrow {
  margin-left:4px;
  height: 16px;
  width: 16px;
  vertical-align: text-bottom;
}

.itemCategory {
  margin-top: 2px;
  margin-bottom: 2px;
}

.item {
  margin-left: 40px;
  margin-bottom: 16px;
  margin-top: 4px;
  padding: 16px;
  border: 1px solid #888
}

.itemTitle {
  font-size: 16px;
  color: #3a90ca;
  margin-left: 16px;
}

.typeFilter {
  margin-left: 32px;
}

.typeFilterSelected {
  color: #63c0f5
}

.inputFilter {
  margin-top: 0px;
  margin-bottom:20px;
  padding: 0px;
  color: #fff;
}

.App {
  display: "block";
  padding-top: 40px;
  margin: 0 auto;
  max-width: 1200px;
}

.App-items {
  padding-top: 0;
}

header h1:before {
  content: url("icon.png");
  margin-right:8px;
  vertical-align: text-top;
}

body {
  margin: 0;
  padding: 0;
  background: #151515;
  color: #eaeaea;
  font-size: 16px;
  line-height: 1.5;
  font-family: Monaco, "Bitstream Vera Sans Mono", "Lucida Console", Terminal, monospace
}

.container {
  width: 90%;
  max-width: 1000px;
  margin: 0 auto
}

section {
  display: block;
  margin: 0 0 20px 0
}

h1,h2,h3,h4,h5,h6 {
  margin: 0 0 20px
}

li {
  line-height: 1.4
}

header {
  background: rgba(0,0,0,0.1);
  width: 100%;
  border-bottom: 1px dashed #666;
  padding: 20px 0;
}

header h1 {
  font-size: 30px;
  line-height: 1.5;
  margin: 0 0 0 -40px;
  font-weight: bold;
  font-family: Monaco, "Bitstream Vera Sans Mono", "Lucida Console", Terminal, monospace;
  color: #2d6e99;
  text-shadow: 0 1px 1px rgba(0,0,0,0.1),0 0 5px rgba(181,232,83,0.1),0 0 10px rgba(181,232,83,0.1);
  letter-spacing: -1px;
  -webkit-font-smoothing: antialiased
}

@media (max-width: 1000px) {
  header h1 {
      margin-left:0
  }
}

header h2 {
  font-size: 18px;
  font-weight: 300;
  color: #666
}

section img {
  max-width: 100%
}

h1,h2,h4,h5,h6 {
  font-weight: normal;
  font-family: Monaco, "Bitstream Vera Sans Mono", "Lucida Console", Terminal, monospace;
  color: #2d6e99;
  letter-spacing: -0.03em;
  text-shadow: 0 1px 1px rgba(0,0,0,0.1),0 0 5px rgba(181,232,83,0.1),0 0 10px rgba(181,232,83,0.1)
}

h3 {
  font-weight: normal;
  font-family: Monaco, "Bitstream Vera Sans Mono", "Lucida Console", Terminal, monospace;
  color: #9b5923;
  letter-spacing: -0.03em;
  text-shadow: 0 1px 1px rgba(0,0,0,0.1),0 0 5px rgba(181,232,83,0.1),0 0 10px rgba(181,232,83,0.1)
}

blockquote {
  color: #aaa;
  padding-left: 10px;
  border-left: 1px dotted #666
}

